import React from 'react';
import './card-overlay.scss';

const CardOverlay = props => {
	return (
		<div
			className={`card__overlay overlay ${props.isHidden}  ${props.className}`}
		>
			<button onClick={props.closeHandler}>&#10006;</button>

			<h5>{props.title}</h5>
			{props.text.map((paragraph, index) => {
				return <p key={index}>{paragraph}</p>;
			})}
		</div>
	);
};

export default CardOverlay;
